$BG_COLOR: $bg3;
$TEXT_COLOR: $fg3;

// text shadow arguments: h-offset, v-offset, blur, color
$TEXT_SHADOW_GEOMETRY: 0 0 .1em;
// shadow arguments: h-offset, v-offset, blur, spread, color
$SHADOW_GEOMETRY: 0 0 .5em .1em;
$SHADOW_COLOR: scale-color($ambience, $lightness: -50%, $alpha: -50%);
// text shadow arguments: h-offset, v-offset, blur, color
$TEXT_GLOW_GEOMETRY: 0 0 .1em;
// shadow arguments: h-offset, v-offset, blur, spread, color
$GLOW_GEOMETRY: 0 0 .5em .1em;
@function GLOW_COLOR($color) {
  @return $color;
}
$LINK_COLOR: dual_value($hue_blue, $hue_cyan);

@mixin content_normal {
  color: $TEXT_COLOR;
  background-color: $BG_COLOR;
  font-family: $font_sans;
  font-style: normal;
  font-variant: normal;
  font-weight: 300;
  font-size: 1em;
}
@mixin content_inverted {
  //@include content_normal;
  color: $BG_COLOR;
  background-color: $TEXT_COLOR;
}

@mixin clear_box_shadow {
  box-shadow: 0 0 0 0 rgba(0,0,0,0);
}
@mixin with_box_shadow {
  box-shadow: $SHADOW_GEOMETRY $SHADOW_COLOR;
}
@mixin with_box_glow($base_color) {
  box-shadow: $GLOW_GEOMETRY $base_color;
}
@mixin with_box_shadow_or_glow($base_color) {
  @if $CURRENT_BRIGHTNESS == ambient {
    @include with_box_glow($base_color);
  } @else if $CURRENT_BRIGHTNESS == radiant {
    @include with_box_shadow;
  } @else {
    @error 'Unsupported color variant of "#{$CURRENT_BRIGHTNESS}".  Use only "ambient" or "radiant."';
  }
}
@mixin clear_text_shadow {
  text-shadow: 0 0 0 rgba(0,0,0,0);
}
@mixin with_text_shadow {
  text-shadow: $TEXT_SHADOW_GEOMETRY $SHADOW_COLOR;
}
@mixin with_text_glow($base_color) {
  text-shadow: $TEXT_GLOW_GEOMETRY $base_color;
}
@mixin with_text_shadow_or_glow($base_color) {
  @if $CURRENT_BRIGHTNESS == ambient {
    @include with_text_glow($base_color);
  } @else if $CURRENT_BRIGHTNESS == radiant {
    @include with_text_shadow;
  } @else {
    @error 'Unsupported color variant of "#{$CURRENT_BRIGHTNESS}".  Use only "ambient" or "radiant."';
  }
}

// visible containers
%visual_container {
  padding-left: 1.5em;
  padding-right: 1.5em;
  padding-top: 1em;
  padding-bottom: 1em;
}

// centered divs
@mixin centered_H {
  left: 50%;
  transform: translateX(-50%);
}
