// declare a single font
@mixin declare-font($name, $style, $weight, $display: swap, $font-path: '/assets/fonts/', $subpath: null) {
  @if $subpath == null {
    $subpath: $name;
  }
  // type checks for enumerations
  @if $style != normal and $style != italic {
    @error 'Unsupported style: "#{$style}"';
  }
  @if     $display != swap
      and $display != auto
      and $display != block
      and $display != fallback
      and $display != optional {
    @error 'Unsupported value for font-display: "#{$display}".
           Check
           https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display';
  }
  font-family: '#{$name}';
  font-style: $style;
  font-weight: $weight;
  font-display: $display;
  $stem: null;
  $stem2: null;
  @if $weight == 100 {
    $stem: Thin;
  } @else if $weight == 300 {
    $stem: Light;
  } @else if $weight == 400 {
    $stem: Regular;
  } @else if $weight == 500 {
    $stem: Medium;
  } @else if $weight == 700 {
    $stem: Bold;
  } @else if $weight == 900 {
    $stem: Black;
  } @else {
    @error 'Unsupported weight number: #{$weight}';
  }
  @if $style == 'italic' {
    @if $weight == 400 {
      $stem: Italic;
      $stem2: Italic;
    } @else {
      $stem2: #{$stem}Italic;
      $stem: #{$stem} Italic;
    }
  } @else {
    $stem2: #{$stem};
  }
  src:
    local('#{$name} #{$stem}'), local('#{$subpath}-#{$stem2}'),
    url('#{$font_path}#{$subpath}/#{$subpath}-#{$stem2}.ttf') format('ttf');
}

// declare a font family with multiple styles and weights
@mixin declare-font-family($name, $styles, $weights, $display: swap, $font-path: '/assets/fonts/', $subpath: null) {
  @each $wt in $weights {
    @each $sty in $styles {
      @font-face {
        @include declare-font($name, $sty, $wt, $display, $font-path, $subpath);
      }
    }
  }
}
