html {
  @include content_normal;
}
::selection {
  @include content_inverted;
}
div.panel, div.card {
  @extend %visual_container;
}
div.card {
  @include with_box_shadow;
}
a {
  color: $LINK_COLOR;
  text-decoration: none;
}
a:hover {
  @include with_text_glow($LINK_COLOR);
}
div.panel-header {
  margin: auto;
  width: 80%;
}
h1, h2, h3, h4, h5, h6 {
  font-family: $font_serif;
  color: $TEXT_COLOR;
  @include with_text_shadow_or_glow($TEXT_COLOR);
}
img, video {
  @include with_box_shadow;
  max-height: 10em;
  max-width: 100%;
  width: auto;
}
video {
  max-height: 20em;
}

///////////////////////////////////////////////////////////////////////////////
// nav bar
div.nav-bar-container {
  display: flex;
  justify-content: center;
}
div.nav-bar {
  font-family: $font_narrow;
  text-align: center;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
nav * {
  margin: 0;
  padding: 0.1em 0.5em;
  color: $LINK_COLOR;
}
@mixin nav_underline {
  border-bottom-style: solid;
  border-bottom-width: 0.15em;
  border-bottom-color: $LINK_COLOR;
}
nav .current {
  @include nav_underline;
}
$NAV_HOVER_ANIM_DURATION: .1s;
@mixin nav-hover-style {
  background-color: $LINK_COLOR;
  color: $BG_COLOR;
  transition:
    //background-color $NAV_HOVER_ANIM_DURATION,
    //color $NAV_HOVER_ANIM_DURATION,
    //border-bottom-color $NAV_HOVER_ANIM_DURATION,
    box-shadow $NAV_HOVER_ANIM_DURATION;
  @include nav_underline;
  @include with_box_shadow_or_glow($LINK_COLOR);
  @include with_text_shadow_or_glow($BG_COLOR);
}
nav a:hover {
  @include nav-hover-style;
}
