///////////////////////////////////////////////////////////////////////////////
// main body
div.panel-body {
  display: flex;
}
div.panel-sidebar {
  margin: auto;
  width: 10%;
}
div.panel-content {
  margin: auto;
  width: 80%;
}

///////////////////////////////////////////////////////////////////////////////
// publications
span.publication-group-header {
  font-family: $font_sans;
  font-weight: 100;
}
div.publication-group-content-container {
  padding-left: 3em;
}
div.publication-container {
  margin: 1.5em 0;
  font-size: 0.8em;
  color: $fg0;
  font-weight: 100;
  span.title {
    color: $TEXT_COLOR;
    font-family: $font_serif;
    font-size: 1.2em;
    line-height: 1.2;
    @include with_text_shadow_or_glow($TEXT_COLOR);
  }
  span.author.myself {
    //@include with_text_shadow_or_glow($TEXT_COLOR);  // can use this to highlight
  }
  span.venue {
    font-style: italic;
  }
  span.resources {
    display: block;
  }
}
span.title, span.authors {
  display: block;  // so that they are on separate lines
}

///////////////////////////////////////////////////////////////////////////////
// project listings
div.project-container {
  // hero image of the div
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  padding: 1em 1em 1em 1em;
  margin-top: 2em;
  margin-bottom: 2em;
  @include with_box_shadow;
  span {
    padding: .2em .2em;
    background-color: $BG_COLOR;
    line-height: 1.5em;
  }
  .project-title {
  }
  .project-description {
    font-style: italic;
  }
  a.project-detail-link {
  }
}

///////////////////////////////////////////////////////////////////////////////
