$DEBUG: true;  // debug mode could be always on since build time is not yet a bottleneck

// pull in all theme components
// theme import is order-sensitive
@import "themes/colors/nordic";  // import palette before setting brightness
@import "util/color_util";  // brightness variant
//@include set-brightness(ambient);
@import "themes/fonts/creamy";
@import "themes/styles/flat";  // need to load color and font first
@import "themes/constructs/cards";  // need to load styles first
@import "themes/semantics/with_love";  // need to load constructs first

@if $DEBUG {
  @debug "$bg3 : #{$bg3}";
  @debug "$bg2 : #{$bg2}";
  @debug "$bg1 : #{$bg1}";
  @debug "$bg0 : #{$bg0}";
  @debug "$fg0 : #{$fg0}";
  @debug "$fg1 : #{$fg1}";
  @debug "$fg2 : #{$fg2}";
  @debug "$fg3 : #{$fg3}";
}
