@import 'util/font_declarations';

// Cuprum font family
@include declare-font-family(Cuprum, [normal, italic], [400, 700]);
// Merriweather font family
@include declare-font-family(Merriweather, [normal, italic], [300, 400, 700, 900]);
// Roboto font family
@include declare-font-family(Roboto, [normal, italic], [100, 300, 400, 500, 700, 900]);
// Ubuntu Mono font family
@include declare-font-family('Ubuntu Mono', [normal, italic], [400, 700], $subpath: UbuntuMono);

// font aliases
$font_narrow: "Cuprum";
$font_serif: "Merriweather", serif;
$font_sans: "Roboto", sans-serif;
$font_mono: "Ubuntu Mono", monospace;
