// given a color palette with standard naming of
// ambience*, radiance*, hue_*, accent_*,
// convert ambience* and radiance* to bg* and fg* color aliases

// default light variance
$CURRENT_BRIGHTNESS: radiant;

// default brightness is radiance
$bg3: $radiance;
$bg2: $radiance_dark;
$bg1: $radiance_darker;
$bg0: $radiance_darkest;
$fg0: $ambience_lightest;
$fg1: $ambience_lighter;
$fg2: $ambience_light;
$fg3: $ambience;

@mixin set-brightness($brightness) {
  @if $brightness == ambient {
    @if $DEBUG {
      @debug "setting ambient";
    }
    $CURRENT_BRIGHTNESS: ambient !global;
    $fg3: $radiance          !global;
    $fg2: $radiance_dark     !global;
    $fg1: $radiance_darker   !global;
    $fg0: $radiance_darkest  !global;
    $bg0: $ambience_lightest !global;
    $bg1: $ambience_lighter  !global;
    $bg2: $ambience_light    !global;
    $bg3: $ambience          !global;
  } @else if $brightness == radiant {
    @if $DEBUG {
      @debug "setting radiant";
    }
    $CURRENT_BRIGHTNESS: radiant !global;
    $bg3: $radiance          !global;
    $bg2: $radiance_dark     !global;
    $bg1: $radiance_darker   !global;
    $bg0: $radiance_darkest  !global;
    $fg0: $ambience_lightest !global;
    $fg1: $ambience_lighter  !global;
    $fg2: $ambience_light    !global;
    $fg3: $ambience          !global;
  } @else {
    @error 'Unsupported $brightness of "#{$brightness}".  Use only "ambient" or "radiant."';
  }
}

@function dual_value($radiant_value, $ambient_value) {
  @if $CURRENT_BRIGHTNESS == ambient {
    @return $ambient_value;
  } @else if $CURRENT_BRIGHTNESS == radiant {
    @return $radiant_value;
  } @else {
    @error 'Unsupported color variant of "#{$CURRENT_BRIGHTNESS}".  Use only "ambient" or "radiant."';
  }
}
